import { _decorator, Component, Node, Button } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('MenuView')
export class MenuView extends Component {
    @property(Button)
    private playGameBtn: Button;

    @property(Button)
    private settingBtn: Button;

    @property(Button)
    private continueBtn: Button;

    public get PlayGameBtn() : Button {
        return this.playGameBtn
    }

    public set PlayGameBtn(playGameBtn : Button) {
        this.playGameBtn = playGameBtn;
    }

    public get SettingBtn() : Button {
        return this.settingBtn
    }

    public set SettingBtn(settingBtn : Button) {
        this.settingBtn = settingBtn;
    }

    public get ContinueBtn() : Button {
        return this.continueBtn
    }

    public set ContinueBtn(continueBtn : Button) {
        this.continueBtn = continueBtn;
    }

    start() {

    }

    update(deltaTime: number) {
        
    }
}

